int, define the maximum width of the container in a media query\n=make-container-max-widths($max-widths: $container-max-widths, $breakpoints: $grid-breakpoints)\n  @each $breakpoint, $container-max-width in $max-widths\n    +media-breakpoint-up($breakpoint, $breakpoints)\n      max-width: $container-max-width\n\n=make-row($gutter: $grid-gutter)\n  display: flex\n  flex-wrap: wrap\n  flex: 1 1 auto\n  margin: -$gutter / 2\n\n=make-col-ready($gutter: $grid-gutter)\n  // Prevent columns from becoming too narrow when at smaller grid tiers by\n  // always setting `width: 100%;`. This works because we use `flex` values\n  // later on to override this initial width.\n  width: 100%\n  padding: $gutter / 2\n\n=make-col($size, $columns: $grid-columns)\n  flex: 0 0 percentage($size / $columns)\n  // Add a `max-width` to ensure content within each column does not blow out\n  // the width of the column. Applies to IE10+ and Firefox. Chrome and Safari\n  // do not appear to require this.\n  max-width: percentage($size / $columns)\n\n=make-col-offset($size, $columns: $grid-columns)\n  $num: $size / $columns\n  +ltr()\n    margin-left: if($num == 0, 0, percentage($num))\n  +rtl()\n    margin-right: if($num == 0, 0, percentage($num))\n\n=make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter, $breakpoints: $grid-breakpoints)\n  // Common properties for all breakpoints\n  %grid-column\n    width: 100%\n    padding: $gutter / 2\n  @each $breakpoint in map-keys($breakpoints)\n    $infix: breakpoint-infix($breakpoint, $breakpoints)\n    // Allow columns to stretch full width below their breakpoints\n    @for $i from 1 through $columns\n      .col#{$infix}-#{$i}\n        @extend %grid-column\n    .col#{$infix},\n    .col#{$infix}-auto\n      @extend %grid-column\n    +media-breakpoint-up($breakpoint, $breakpoints)\n      // Provide basic `.col-{bp}` classes for equal-width flexbox columns\n      .col#{$infix}\n        flex-basis: 0\n        flex-grow: 1\n        max-width: 100%\n      .col#{$infix}-auto\n        flex: 0 0 auto\n        width: auto\n        max-width: 100% // Reset earlier grid tiers\n      @for $i from 1 through $columns\n        .col#{$infix}-#{$i}\n          +make-col($i, $columns)\n      // `$columns - 1` because offsetting by the width of an entire row isn't possible\n      @for $i from 0 through $columns - 1\n        @if not ($infix == \"\" and $i == 0)\n          // Avoid emitting useless .offset-0\n          .offset#{$infix}-#{$i}\n            +make-col-offset($i, $columns)\n","@import '../tools/_functions.sass';\n\n$color-pack: true !default;\n\n$body-font-family: 'Roboto', sans-serif !default;\n$font-size-root: 16px !default;\n$line-height-root: 1.5 !default;\n$border-radius-root: 4px !default;\n\n$rounded: () !default;\n$rounded: map-deep-merge(\n  (\n    0: 0,\n    'sm': $border-radius-root / 2,\n    null: $border-radius-root,\n    'lg': $border-radius-root * 2,\n    'xl': $border-radius-root * 6,\n    'pill': 9999px,\n    'circle': 50%\n  ),\n  $rounded\n);\n\n$spacer: 4px !default;\n$spacers-steps: 16 !default; \n\n$spacers: () !default;\n@if (type-of($spacers) == list) {\n  @for $i from 0 through $spacers-steps {\n    $spacers: map-merge($spacers, ($i: $spacer * $i))\n  }\n}\n\n$negative-spacers: () !default;\n@if (type-of($negative-spacers) == list) {\n  @for $i from 1 through $spacers-steps {\n    $negative-spacers: map-merge($negative-spacers, (\"n\" + $i: -$spacer * $i))\n  }\n}\n\n$grid-breakpoints: () !default;\n$grid-breakpoints: map-deep-merge(\n  (\n    'xs': 0,\n    'sm': 600px,\n    'md': 960px,\n    'lg': 1280px - 16px,\n    'xl': 1920px - 16px\n  ),\n  $grid-breakpoints\n);\n\n$grid-gutter: $spacer * 6 !default;\n$form-grid-gutter: $spacer * 2 !default;\n$grid-columns: 12 !default;\n\n$container-padding-x: $grid-gutter / 2 !default;\n\n$grid-gutters: () !default;\n$grid-gutters: map-deep-merge(\n  (\n    'xs': $grid-gutter / 12,\n    'sm': $grid-gutter / 6,\n    'md': $grid-gutter / 3,\n    'lg'